import {
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output
} from '@angular/core';
import {ResizeService} from './resize.service';

@Directive({
  selector: '[adaptResize]',
  exportAs: 'adaptResize',
})
export class AdaptResizeDirective implements OnInit, OnDestroy {

  @Input('adaptResize') elementToListen: HTMLElement;
  @Output() readonly elementResize: EventEmitter<void> = new EventEmitter();

  constructor(
    private _el: ElementRef,
    private _resizeService: ResizeService
  ) {
  }

  ngOnInit(): void {
    this.elementToListen = this.elementToListen || this._el.nativeElement;
    this._resizeService.addResizeEventListener(this.elementToListen, () => {
      this.elementResize.emit();
    });
  }

  ngOnDestroy(): void {
    this._resizeService.removeResizeEventListener(this.elementToListen);
  }
}
